.background {
  background: -webkit-gradient(linear, left bottom, right top, from(#0083dd), color-stop(#377bd8), color-stop(#4f72d2), color-stop(#6269ca), to(#7160c1));
  background: linear-gradient(to right top, #0083dd, #377bd8, #4f72d2, #6269ca, #7160c1);
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;
}



