.userImage {
  width: 180px;
  height: 180px;
  top: 80px;
  position: absolute;
  -webkit-box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, .5);
          box-shadow: 5px 5px 25px 0 rgba(46, 61, 73, .5);
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  background: -webkit-gradient(linear, left top, right top, from(#0083dd), color-stop(#377bd8), color-stop(#4f72d2), color-stop(#6269ca), to(#7160c1));
  background: linear-gradient(to right, #0083dd, #377bd8, #4f72d2, #6269ca, #7160c1);
}

.userImage:hover {
  -webkit-box-shadow: 2px 2px 15px 0 rgba(46, 61, 73, .5);
          box-shadow: 2px 2px 15px 0 rgba(46, 61, 73, .5);
}

.userCard {
  margin-top: 110px;
}



